import bcrypt from 'bcryptjs';
import { v4 as uuidv4 } from 'uuid';
import { DatabaseService } from '../database';

export class AuthService {
  // Hash password
  static async hashPassword(password) {
    try {
      const saltRounds = 12;
      return await bcrypt.hash(password, saltRounds);
    } catch (error) {
      console.error('Error hashing password:', error);
      throw error;
    }
  }

  // Verify password
  static async verifyPassword(password, hash) {
    try {
      return await bcrypt.compare(password, hash);
    } catch (error) {
      console.error('Error verifying password:', error);
      throw error;
    }
  }

  // Generate session token
  static generateSessionToken() {
    return uuidv4();
  }

  // Store session in localStorage
  static setSession(sessionData) {
    try {
      localStorage.setItem('luminate_session', JSON.stringify(sessionData));
    } catch (error) {
      console.error('Error storing session:', error);
    }
  }

  // Get session from localStorage
  static getSession() {
    try {
      const session = localStorage.getItem('luminate_session');
      return session ? JSON.parse(session) : null;
    } catch (error) {
      console.error('Error getting session:', error);
      return null;
    }
  }

  // Clear session
  static clearSession() {
    try {
      localStorage.removeItem('luminate_session');
    } catch (error) {
      console.error('Error clearing session:', error);
    }
  }

  // Check if user is authenticated
  static isAuthenticated() {
    const session = this.getSession();
    return session && session.user && session.token;
  }

  // Get current user
  static getCurrentUser() {
    const session = this.getSession();
    return session ? session.user : null;
  }

  // Sign up new user
  static async signUp(userData) {
    try {
      const { firstName, lastName, email, password, role, licenseNumber } = userData;

      // Check if user already exists
      const existingUser = await DatabaseService.getUserByEmail(email);
      if (existingUser) {
        throw new Error('User with this email already exists');
      }

      // Hash password
      const passwordHash = await this.hashPassword(password);

      // Create user data
      const newUserData = {
        firstName,
        lastName,
        email,
        passwordHash,
        role,
        licenseNumber: role === 'clinician' ? licenseNumber : null
      };

      // Create user in database
      const user = await DatabaseService.createUser(newUserData);

      // Generate session token
      const token = this.generateSessionToken();

      // Create session data
      const sessionData = {
        user: {
          id: user.id,
          firstName: user.firstName,
          lastName: user.lastName,
          email: user.email,
          role: user.role,
          licenseNumber: user.licenseNumber
        },
        token,
        createdAt: new Date().toISOString()
      };

      // Store session
      this.setSession(sessionData);

      return {
        success: true,
        user: sessionData.user,
        message: 'Account created successfully'
      };
    } catch (error) {
      console.error('Error signing up user:', error);
      return {
        success: false,
        error: error.message || 'Failed to create account'
      };
    }
  }

  // Sign in user
  static async signIn(email, password) {
    try {
      // Get user from database
      const user = await DatabaseService.getUserByEmail(email);
      if (!user) {
        throw new Error('Invalid email or password');
      }

      // Verify password
      const isValidPassword = await this.verifyPassword(password, user.passwordHash);
      if (!isValidPassword) {
        throw new Error('Invalid email or password');
      }

      // Generate session token
      const token = this.generateSessionToken();

      // Create session data
      const sessionData = {
        user: {
          id: user.id,
          firstName: user.firstName,
          lastName: user.lastName,
          email: user.email,
          role: user.role,
          licenseNumber: user.licenseNumber
        },
        token,
        createdAt: new Date().toISOString()
      };

      // Store session
      this.setSession(sessionData);

      return {
        success: true,
        user: sessionData.user,
        message: 'Signed in successfully'
      };
    } catch (error) {
      console.error('Error signing in user:', error);
      return {
        success: false,
        error: error.message || 'Failed to sign in'
      };
    }
  }

  // Sign out user
  static signOut() {
    try {
      this.clearSession();
      return {
        success: true,
        message: 'Signed out successfully'
      };
    } catch (error) {
      console.error('Error signing out user:', error);
      return {
        success: false,
        error: 'Failed to sign out'
      };
    }
  }

  // Update user profile
  static async updateProfile(userId, updateData) {
    try {
      const updatedUser = await DatabaseService.updateUser(userId, updateData);
      
      // Update session if current user
      const session = this.getSession();
      if (session && session.user.id === userId) {
        session.user = {
          ...session.user,
          ...updateData
        };
        this.setSession(session);
      }

      return {
        success: true,
        user: updatedUser,
        message: 'Profile updated successfully'
      };
    } catch (error) {
      console.error('Error updating profile:', error);
      return {
        success: false,
        error: error.message || 'Failed to update profile'
      };
    }
  }

  // Change password
  static async changePassword(userId, currentPassword, newPassword) {
    try {
      // Get current user
      const user = await DatabaseService.getUserById(userId);
      if (!user) {
        throw new Error('User not found');
      }

      // Verify current password
      const isValidPassword = await this.verifyPassword(currentPassword, user.passwordHash);
      if (!isValidPassword) {
        throw new Error('Current password is incorrect');
      }

      // Hash new password
      const newPasswordHash = await this.hashPassword(newPassword);

      // Update user with new password
      await DatabaseService.updateUser(userId, { passwordHash: newPasswordHash });

      return {
        success: true,
        message: 'Password changed successfully'
      };
    } catch (error) {
      console.error('Error changing password:', error);
      return {
        success: false,
        error: error.message || 'Failed to change password'
      };
    }
  }

  // Validate session
  static validateSession() {
    try {
      const session = this.getSession();
      if (!session || !session.user || !session.token) {
        return false;
      }

      // Check if session is expired (24 hours)
      const sessionAge = Date.now() - new Date(session.createdAt).getTime();
      const maxAge = 24 * 60 * 60 * 1000; // 24 hours

      if (sessionAge > maxAge) {
        this.clearSession();
        return false;
      }

      return true;
    } catch (error) {
      console.error('Error validating session:', error);
      return false;
    }
  }

  // Refresh session
  static async refreshSession() {
    try {
      const session = this.getSession();
      if (!session) {
        return false;
      }

      // Get fresh user data from database
      const user = await DatabaseService.getUserById(session.user.id);
      if (!user) {
        this.clearSession();
        return false;
      }

      // Update session with fresh user data
      const updatedSession = {
        ...session,
        user: {
          id: user.id,
          firstName: user.firstName,
          lastName: user.lastName,
          email: user.email,
          role: user.role,
          licenseNumber: user.licenseNumber
        },
        createdAt: new Date().toISOString()
      };

      this.setSession(updatedSession);
      return true;
    } catch (error) {
      console.error('Error refreshing session:', error);
      return false;
    }
  }
}

export default AuthService;
